// $("a[href*='tel:']").on("click", function(e){
//   if(ua.getDevice() == 'pc'){
//     e.preventDefault();
//   }
// });

const targets = document.querySelectorAll("a[href*='tel:']");

for(let target of targets) {
  target.addEventListener("click", (e) => {
    if(ua.getDevice() == 'pc'){
      e.preventDefault();
    }
  });
}
