// import 'nodelist-foreach-polyfill';
import 'lazyload';

let images = document.querySelectorAll(".lazyload");
lazyload(images, {
  rootMargin: "300px"
});

//ajax
$(document).ajaxSuccess(function (event, xhr, settings) {
  let images = document.querySelectorAll(".lazyload");
  lazyload(images, {
    rootMargin: "300px"
  });
});